@import './variables';

/* Scss Document */

.pageWrapper {
    max-width: 1440px;
}

input[type=button],
.button {
    cursor: pointer;
    border-radius: 6px;
    // box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), inset 0 2px 0 0 #ffffff;
    font-family: 'Lato', sans-serif;
    font-weight: 900;
    font-size: 1.2em;
    padding: 4px 20px;
    text-align: center;
    min-width: 8em;

    border: solid 1px $color-brand-blue;
    color: $actual-white;
    background-color: $color-brand-blue;
    &.disabled {
        border-color: $color-warm-grey;
        color: $color-warm-grey;
        margin: 0 auto!important;
    }
}

/* -- HOME PAGE -- */

#hero {
    background-image: url(../images/home.hero.1.png);
    height:685px;
    width:100%;
    max-width: 1440px;

}
#app-block {
    background-image: url(../images/intro-background.png);
    height:342px;
    width:100%;
    max-width: 1440px;
}
#cta-block {
    background-image: url(../images/outro-background.png);
